.logo-wrapper {
  background-color: rgba(200, 27, 53, 0.8);
  color: #fff;
  display: flex;
  justify-content:  center;
  align-items: center;
  flex-direction: column;
  border-radius: 10px;
  padding: .2em .5em;
  text-decoration: none;
}

h1 {
  font-size: 2rem;
}

.logo-wrapper p {
  font-size: 0.7em;
}

/* Media Query: Max Width 1000px  -------------------------------------*/

@media screen and (max-width: 1000px) {
  h1 {
    font-size: 1em;
  }
}