.burger-btn-wrapper {
  justify-content: center;
  align-items: center;
  z-index: 11111;
  position: relative;
  transition: all 500ms;
  display: none;
  cursor: pointer;
}

.burger-btn {
  background: #fff;
  border: none;
  outline: none;
  width: 30px;
  height: 3px;
  border-radius: 10px;
  transition: all 500ms;
}

.burger-btn-wrapper:before {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 6px;
  left: 0;
  background-color: #fff;
  border-radius: 10px;
  transition: all 500ms;
}

.burger-btn-wrapper:after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 12px;
  left: 0;
  background-color: #fff;
  border-radius: 10px;
  transition: all 500ms;
}

.burger-btn-close {
  transform: rotateZ(45deg) translateY(9px);
}

.btn-wrapper-close::before {
  opacity: 0;
}

.btn-wrapper-close::after {
  transform: rotateZ(-45deg) translateY(-9px);
}

/* Media Query: Max Width 650px  -------------------------------------*/

@media screen and (max-width: 650px) {
  .burger-btn-wrapper {
    display: flex;
  }
}