.reviews-section {
  margin-top: 1rem;
  padding: 1em 0;
}

.reviews-section h2 {
  margin-bottom: 1em;
}

.reviews {
  display: flex;
  flex-wrap: nowrap;
  gap: 2em;
  align-items: flex-start;
  padding: 0.5em;
  overflow-x: auto;
}

.reviews::-webkit-scrollbar {
  height: 4px;
}

.reviews::-webkit-scrollbar-track {
  background-color: rgba(163, 163, 163, 0.1);
  border-radius: 10px;
}

.reviews::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.8);
  border-radius: 10px;
}