footer {
  background-color: rgb(222, 222, 222);
  width: 100%;
  padding: .5rem;
  display: flex;
  gap: 2em;
  align-items: center;
  justify-content: space-between;
}

footer a {
  text-decoration: none;
}

footer li {
  list-style: none;
}

.privacy {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: .7em;
}

.social-media-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5em;
}

.social-media-container ul {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: .7em 1.2em;
}

.fa-brands {
  font-size: 1.7em;
}

.site-map {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.site-map a {
  font-size: 0.9em;
}

.amazon-logo {
  width: 150px;
  height: 100px;
  background: url('./aws-logo.png');
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
}


/* Media Query: Max Width 830px  -------------------------------------*/

@media screen and (max-width: 830px) {
  footer {
    flex-direction: column;
  }

  .privacy {
    font-size: 1.2em;
  }

  .site-map a {
    font-size: 1.2em;
  }
}